import React, { Component } from "react";
import styles from "./Contact.module.scss";

export class Contact extends Component {
  render() {
    return (
      <div className={styles.container}>
        <form className={styles.container__form}>
          <span id="name-label">Name</span>
          <input type="input" id="name" placeholder="Bruce Wayne" />

          <span id="email-label">Email</span>
          <input type="input" id="email" placeholder="batman321@hotmail.com" />

          <span id="message-label">Message</span>
          <textarea id="message" rows="8" placeholder="You look great today." />

          <input type="button" value="Send" id="send" />
        </form>
      </div>
    );
  }
}

export default Contact;
